import './App.css';
import ProfilePic from './Component/ProfilePic'
import Social from './Component/Social'
import Counter from './Component/Counter'
import Display from './Component/Display'
import { useState } from 'react';
import Button from './Component/Button'


function App() {

  const [about, setAbout] = useState("My name is Dinah")

  const handleName =()=>{
    setAbout("My name is Dinah")
  }

  const handleSkills =()=>{
    setAbout("I'm a Frontend Developer")
  }

  return (
    <div className="App">
        <main className="wrapper">
          <div className="profile-image">
            <div className="inner-area">
              <ProfilePic />
            </div>
          </div>
          <div className="greeting"><h1>Hello!</h1></div>
         
          <Display text={about}/>

          <div className ="buttons">
           <Button clickhandler ={handleName} btntext={"Name"} />
           <Button clickhandler ={handleSkills} btntext={"Skills"} />
          </div>

          <Social />
          <Counter />
        </main>
       
    </div>
  );
}

export default App;
